@function rem($px) {
	@return calc($px / 16px) + 0rem;
}

@mixin laptop() {
	@media screen and (max-width: 1280px) {
			@content;
	}
}


@mixin tablets() {
	@media screen and (max-width: 1000px) {
			@content;
	}
}

@mixin game-tablets() {
	@media screen and (max-width: 768px) {
			@content;
	}
}


@mixin phones() {
	@media screen and (max-width: 500px) {
			@content;
	} 
}